/*
  This boilerplate is based in ITCSS and SMACSS
*/
@import url("https://fonts.googleapis.com/css?family=Poppins");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:700");
*,
*::before,
*:after {
  box-sizing: border-box;
}

.gallery-container {
  display: none;
}

body {
  margin: 0;
  line-height: 1.6;
  font-family: "Poppins", sans-serif;
  background-color: #FCFCFC;
  color: #53575A;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  width: 100%;
  display: block;
}

.lightbox {
  position: relative;
}

.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  z-index: 20;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.lightbox-container {
  text-align: center;
  border-radius: 5px;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  margin: 0;
}

@media screen and (min-width: 1024px) {
  .lightbox-container {
    width: 33%;
    margin: 0 auto;
    height: 90%;
  }
}

.lightbox-description {
  color: #DD3F3B;
  font-weight: 600;
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100%;
  transform: translate(-50%);
  background: rgba(0, 0, 0, 0.5);
  border-top: 3px solid rgba(0, 0, 0, 0.9);
}

.lightbox-navigation__counter {
  font-size: .8em;
}

.lightbox-image {
  transition: all .7s ease;
}

.lightbox-image.animated-l {
  animation: opacity .5s linear alternate;
}

.lightbox-image.animated-r {
  animation: opacity .5s linear alternate;
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.next {
  position: absolute;
  top: 45%;
  right: 0;
  opacity: 1;
}

.next.disabled {
  opacity: 0;
}

.next::after {
  content: url("https://image.flaticon.com/icons/svg/180/180000.svg");
  position: absolute;
  top: 0;
  right: 0;
  width: 35px;
  height: 35px;
  background: white;
  border-radius: 50%;
  z-index: 10;
  border: 1px solid #FFF;
}

.prev {
  position: absolute;
  top: 45%;
  left: 0;
}

.prev::after {
  content: url("https://image.flaticon.com/icons/svg/179/179983.svg");
  position: absolute;
  top: 0;
  left: 10%;
  width: 35px;
  height: 35px;
  z-index: 10;
  background: white;
  border-radius: 50%;
  border: 1px solid #FFF;
}
